$brand-primary: #079397;
$brand-secondary: #df376e;
$color: #333;
.ct-widget {
    .ct-tag {
        background-color: $brand-secondary !important;
        &:after {
            border-left-color: $brand-secondary !important;
        }
        &:hover {
            background-color: $brand-primary !important;
            &:after {
                border-left-color: $brand-primary !important;
            }
        }
    }
    .ct-tool--subheading3:before {
        content: "H";
        font-size: 15px !important;
    }
    .ct-tool--subheading4:before {
        content: "H";
        font-size: 10px !important;
    }
    .ct-tool--subheading5:before {
        content: "H";
        font-size: 8px !important;
    }
    .ct-tool {
        &.ct-tool--disabled:before {
            color: rgba($color, .5);
        }
        &:before {
            color: $color;
        }
        &:after {
            background: #fff;
            color: #000;
            font-size: 14px;
            bottom:30px;
            word-break: inherit;
            left: 50%;
            -webkit-hyphens: inherit;
            transform: translateX(-50%);
            width: auto;
        }
        &:hover:after {
            opacity: .8;
            visibility: visible;
            transition-property: opacity;
            transition-duration: .3s;
            transition-timing-function: ease-in;
            transition-delay: 1s;
        }
    }
    &.ct-toolbox {
        background: rgba(#fff, .9) !important;
        border: none !important;
        box-shadow: 0 5px 10px rgba(0,0,0, .2) !important;
        width: auto;
        max-width: 248px;
        .ct-grip {
            border-bottom: solid 1px rgba($color, .4);
            width: calc(100% + 16px);
            margin-left: -8px;
            background-image: url(../images/move.svg);
            background-repeat: no-repeat;
            background-position: center center;
            opacity: .8;
            transition: opacity .3s;
            height: 35px;
            margin-top: -10px;
            &:hover {
                opacity: 1;
            }
            .ct-grip__bump {
                display: none;
            }
        }
    }
    &.ct-ignition {
        bottom: 12%;
        right: 5%;
        left: inherit;
        top: inherit;
        display: flex;
        flex-direction: row-reverse;
        width: 170px;
        transform: translateY(-100%) !important;
    }
    .ct-ignition__button {
        transition: box-shadow .5s, opacity .5s, background-color .5s;
        box-shadow: 0 5px 10px rgba(0,0,0, .2);
        opacity: .9;
        &:hover {
            opacity: 1;
            box-shadow: 0 10px 20px rgba(0,0,0, .3);
        }
        &:before {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            background-position: center center;
            background-repeat: no-repeat !important;
            background-size: 40px 40px;
        }
        &[class*='edit'] {
            background-color: $brand-primary;
            &:before {
                transform: none !important;
                background-image: url(../images/pencil.svg);
            }
            &:hover {
                background-color: darken($brand-primary, 2%);
            }
        }
        &[class*='confirm'] {
            background-color: $brand-primary;
            &:before {
                background-image: url(../images/check.svg);
            }
            &:hover {
                background-color: darken($brand-primary, 2%);
            }
        }
        &[class*='cancel'] {
            background-color: $brand-secondary;
            &:before {
                background-image: url(../images/close.svg);
            }
            &:hover {
                background-color: darken($brand-secondary, 2%);
            }
        }
    }
    &.ct-anchored-dialog {
        margin-top: -58px !important;
    }
}
main {
    overflow: hidden;
}
.ce-element--focused, .ce-element--over {
    background-color: transparent;
    position: relative;
    &:before {
        opacity: 1 !important;
    }
}
.ce-element {
    position: relative;
    &:before {
        position: absolute;
        content: "";
        top: -5px;
        left: -10px;
        border: dashed 1px #ccc;
        width: 100%;
        height: calc(100% + 10px);
        transition: opacity .5s;
        border-radius: 5px;
        opacity: 0;
    }
    &.ce-element--focused:before {
        border: dashed 1px #AAA;
    }
}

